import { getConfiguration } from "../application/configuration";
export function createEmptyDocument() {
    return createDocument({
        "iri": "",
        "identifier": "",
        "types": [],
        "title": "",
        "description": "",
        "created": "",
        "properties": {},
        "items": [],
    });
}
export function createDocument(template) {
    const configuration = getConfiguration();
    const identifier = createIdentifier();
    return {
        ...template,
        "iri": configuration.domain + identifier,
        "identifier": identifier,
        "created": new Date().toISOString().substring(0, 10),
    };
}
function createIdentifier() {
    const time = new Date().getTime();
    return time + "-xxxx-yxxx".replace(/[xy]/g, function (c) {
        const r = Math.random() * 16 | 0, v = c == "x" ? r : (r & 0x3 | 0x8);
        return v.toString(16);
    });
}
export function createNote(identifier, text, types = []) {
    return {
        "identifier": identifier,
        "types": types,
        "properties": {},
        "created": new Date().toISOString().substring(0, 10),
        "text": text,
        "image": undefined,
    };
}
